import React, { useState, useEffect, useMemo, useCallback, useRef } from "react";
import { collection, getDocs, query } from "firebase/firestore";
import { db } from "../firebase";
import ProductCard from "../components/ProductCard";
import Filter from "../components/Filter";
import { useSearchParams } from "react-router-dom";

const PAGE_SIZE = 18;

function StorePage() {
  const [initialLoading, setInitialLoading] = useState(true);
  
  // State for all product metadata, fetched once
  const [allProducts, setAllProducts] = useState([]);
  
  // State for master list of filter options, derived from allProducts
  const [masterFilterOptions, setMasterFilterOptions] = useState({
    brands: [],
    gender: [],
    origin: [],
    movement: []
  });

  // State for active filters, synced with URL
  const [activeFilters, setActiveFilters] = useState({
    brands: [],
    gender: [],
    origin: [],
    movement: [],
    priceRange: { min: 0, max: 1000000 } 
  });

  const [isFilterModalOpen, setIsFilterModalOpen] = useState(false);
  const [sortOption, setSortOption] = useState('default');

  const [searchParams, setSearchParams] = useSearchParams();
  
  // State for client-side infinite scroll
  const [displayCount, setDisplayCount] = useState(PAGE_SIZE);
  const observer = useRef();
  const loadingRef = useRef(null);
  
  // Ref to manage scroll restoration
  const isRestoringScroll = useRef(false);

  // New handler to save state when a product is clicked
  const handleProductClick = useCallback(() => {
    const filters = Object.fromEntries([...searchParams]);
    const stateToSave = {
      scrollPosition: window.scrollY,
      filters: filters,
    };
    console.log(`%c[SAVE] Saving state on product click. Scroll: ${stateToSave.scrollPosition}`, 'color: blue; font-weight: bold;', stateToSave);
    sessionStorage.setItem('storePageState', JSON.stringify(stateToSave));
  }, [searchParams]);

  // Fetch all products once on initial load
  useEffect(() => {
    console.log("%c[EFFECT] Main effect running (dependency: searchParams)", "color: green;");
    // On initial load, check if we need to restore scroll
    const savedStateJSON = sessionStorage.getItem('storePageState');
    if (savedStateJSON) {
      console.log("%c[RESTORE] Found saved state in sessionStorage.", "color: orange;");
      const savedState = JSON.parse(savedStateJSON);
      const currentFiltersFromURL = Object.fromEntries([...searchParams]);

      // A simple way to check if filters are the same
      if (JSON.stringify(savedState.filters || {}) === JSON.stringify(currentFiltersFromURL)) {
        console.log("%c[RESTORE] Filters match. Flagging for scroll restoration.", "color: orange;");
        isRestoringScroll.current = true;
      } else {
        // If filters are different, the scroll position is invalid.
        console.warn("[RESTORE] Filters do not match. Removing obsolete saved state.");
        sessionStorage.removeItem('storePageState');
      }
    } else {
        console.log("[RESTORE] No saved state found in sessionStorage.");
    }
    
    // Sync activeFilters from URL search params
    const params = Object.fromEntries([...searchParams]);
    const newFilters = {
      brands: params.brands ? params.brands.split(",") : [],
      gender: params.gender ? params.gender.split(",") : [],
      origin: params.origin ? params.origin.split(",") : [],
      movement: params.movement ? params.movement.split(",") : [],
      priceRange: { 
        min: params.minPrice ? parseInt(params.minPrice, 10) : 0, 
        max: params.maxPrice ? parseInt(params.maxPrice, 10) : 1000000 
      }
    };
    setActiveFilters(newFilters);
    setSortOption(params.sort || 'default');

    const fetchAllProducts = async () => {
      // 1. Check for cached data first
      const cachedData = localStorage.getItem('allProductsCache');
      const cacheTimestamp = localStorage.getItem('allProductsCacheTimestamp');
      const CACHE_DURATION = 7 * 24 * 60 * 60 * 1000; // 7 days

      if (cachedData && cacheTimestamp && (Date.now() - parseInt(cacheTimestamp, 10)) < CACHE_DURATION) {
        console.log("Loading products from cache.");
        const parsedData = JSON.parse(cachedData);
        setAllProducts(parsedData.products);
        setMasterFilterOptions(parsedData.filterOptions);
        setInitialLoading(false);
        return; // Stop here if cache is valid
      }

      console.log("Fetching products from Firestore...");
      setInitialLoading(true);
      try {
        const productsRef = collection(db, "products");
        // A query to fetch all documents. You might want to add a `where("visible", "==", true)` here.
        const q = query(productsRef); 
        const snapshot = await getDocs(q);
        
        const productMetadata = snapshot.docs.map(doc => {
          const data = doc.data();
          // Extract only the fields needed for filtering and display
          return {
            id: doc.id,
            brand: data.brand || data.BRAND,
            gender: data.gender || data.GENDER,
            origin: data.origin || data.ORIGIN,
            movement: data.specifications?.Movement || null,
            price: data.price || data.PRICE || 0,
            imageUrls: data.imageUrls,
            reference: data.reference || data['REF.'],
            muthanaQty: data.muthanaQty || 0,
            royalQty: data.royalQty || 0,
            whQty: data.whQty || 0,
            visible: data.visible !== false,
          };
        });

        const validProducts = productMetadata.filter(p => p.visible && p.brand && p.gender && p.origin && p.price && p.reference && p.imageUrls?.main);
        setAllProducts(validProducts);
        
        // Derive filter options from the fetched products
        const brands = [...new Set(validProducts.map(p => p.brand))].sort();
        const gender = [...new Set(validProducts.map(p => p.gender))].sort();
        const origin = [...new Set(validProducts.map(p => p.origin))].sort();
        const movement = [...new Set(validProducts.map(p => p.movement).filter(Boolean))]
          .filter(m => !m.includes('Hong kong'))
          .sort();

        const derivedFilterOptions = { brands, gender, origin, movement };
        setMasterFilterOptions(derivedFilterOptions);

        // 2. Save the newly fetched data to the cache
        localStorage.setItem('allProductsCache', JSON.stringify({
          products: validProducts,
          filterOptions: derivedFilterOptions
        }));
        localStorage.setItem('allProductsCacheTimestamp', Date.now().toString());

      } catch (error) {
        console.error("Error fetching all products:", error);
      } finally {
        setInitialLoading(false);
      }
    };
    
    fetchAllProducts();
  }, [searchParams]);

  // Client-side filtering logic
  const filteredProducts = useMemo(() => {
    if (initialLoading) {
      return [];
    }
    
    return allProducts.filter(product => {
      const { brands, gender, origin, movement, priceRange } = activeFilters;
      
      const price = typeof product.price === "number" ? product.price : parseInt(product.price.toString().replace(/[^0-9]/g, ""), 10);

      const brandMatch = brands.length === 0 || brands.includes(product.brand);
      const genderMatch = gender.length === 0 || gender.includes(product.gender);
      const originMatch = origin.length === 0 || origin.includes(product.origin);
      const movementMatch = movement.length === 0 || movement.includes(product.movement);
      const priceMatch = price >= priceRange.min && price <= priceRange.max;

      return brandMatch && genderMatch && originMatch && movementMatch && priceMatch;
    });
  }, [activeFilters, allProducts, initialLoading]);

  // Client-side sorting logic
  const sortedProducts = useMemo(() => {
    const sortableProducts = [...filteredProducts];
    switch (sortOption) {
      case 'price-asc':
        sortableProducts.sort((a, b) => a.price - b.price);
        break;
      case 'price-desc':
        sortableProducts.sort((a, b) => b.price - a.price);
        break;
      case 'ref-asc':
        sortableProducts.sort((a, b) => 
          String(a.reference).localeCompare(String(b.reference), undefined, { numeric: true })
        );
        break;
      case 'ref-desc':
        sortableProducts.sort((a, b) => 
          String(b.reference).localeCompare(String(a.reference), undefined, { numeric: true })
        );
        break;
      default:
        break;
    }
    return sortableProducts;
  }, [filteredProducts, sortOption]);

  // When filters or sort order change, reset the number of displayed products and scroll to top (if not restoring)
  useEffect(() => {
    if (!isRestoringScroll.current) {
      window.scrollTo(0, 0);
      setDisplayCount(PAGE_SIZE);
    }
  }, [filteredProducts, sortOption]);

  // Memoize the products to be displayed based on infinite scroll
  const displayedProducts = useMemo(() => {
    return sortedProducts.slice(0, displayCount);
  }, [sortedProducts, displayCount]);

  const hasMore = displayCount < sortedProducts.length;

  // Intersection Observer for infinite scroll
  useEffect(() => {
    const currentObserver = observer.current;
    
    const handleObserver = (entries) => {
      const target = entries[0];
      if (target.isIntersecting && hasMore) {
        setDisplayCount(prevCount => prevCount + PAGE_SIZE);
      }
    };

    if (currentObserver && loadingRef.current) {
      currentObserver.disconnect();
    }
    
    observer.current = new IntersectionObserver(handleObserver, { rootMargin: '200px' });

    if (loadingRef.current) {
      observer.current.observe(loadingRef.current);
    }
    
    return () => {
      if (currentObserver) {
        currentObserver.disconnect();
      }
    };
  }, [hasMore]);

  // Effect to handle scroll restoration
  useEffect(() => {
    console.log(`%c[EFFECT] Restore effect running. Flag: ${isRestoringScroll.current}, Displayed: ${displayedProducts.length}`, "color: green;");

    if (isRestoringScroll.current && displayedProducts.length > 0) {
      const savedState = JSON.parse(sessionStorage.getItem('storePageState'));
      
      if (savedState && savedState.scrollPosition > 0) {
        // Estimate the number of items needed to be rendered for the scroll position to be valid.
        const averageCardHeight = 400; // A reasonable estimate for a product card's height
        const cardsPerRow = 3; 
        const requiredRows = Math.ceil(savedState.scrollPosition / averageCardHeight);
        const requiredCount = Math.max(PAGE_SIZE, requiredRows * cardsPerRow);
        
        console.log(`%c[RESTORE] Scroll position: ${savedState.scrollPosition}px. Requires ~${requiredCount} items. Currently displaying ${displayCount}.`, 'color: orange;');

        // Step 1: Check if we have rendered enough items.
        if (displayCount < requiredCount) {
          console.log(`%c[RESTORE] Not enough items. Setting display count to ${requiredCount} and waiting for re-render.`, 'color: purple; font-weight: bold;');
          setDisplayCount(requiredCount);
          // We exit here. The effect will re-run when `displayedProducts` or `displayCount` changes.
          return; 
        }

        // Step 2: If we have enough items, we can scroll.
        console.log(`%c[RESTORE] Sufficient items are displayed. Scheduling scroll action.`, 'color: orange; font-weight: bold;');
        
        // Using a small timeout to ensure the DOM has caught up with the latest render.
        setTimeout(() => {
          console.log(`%c[RESTORE] EXECUTING scroll to: ${savedState.scrollPosition}px`, 'color: red; font-weight: bold;');
          window.scrollTo({ top: savedState.scrollPosition, behavior: 'auto' });
          isRestoringScroll.current = false; // Mark restoration as complete
          sessionStorage.removeItem('storePageState'); // Clean up
        }, 100); // A small delay is usually sufficient here.
      } else {
         // If there's no scroll position, or it's 0, we're done with restoration.
         isRestoringScroll.current = false;
         if (savedState) {
            sessionStorage.removeItem('storePageState');
         }
      }
    }
  }, [displayedProducts, displayCount]); // Re-run when displayed products or their count changes

  // Calculate dynamic counts for filter options
  const filterOptionsWithCounts = useMemo(() => {
    const calculateCounts = (category) => {
      // Get the master list of options for the category
      const options = masterFilterOptions[category];
      
      return options.map(optionValue => {
        // Count how many products match this option value, given other active filters
        const count = allProducts.filter(product => {
          const { brands, gender, origin, movement, priceRange } = activeFilters;
          
          const price = typeof product.price === "number" ? product.price : parseInt(product.price.toString().replace(/[^0-9]/g, ""), 10);
          const priceMatch = price >= priceRange.min && price <= priceRange.max;

          // Always match the price range
          if (!priceMatch) return false;

          // Match the specific option value for the category we are currently counting
          let selfMatch = false;
          if (category === 'brands') selfMatch = product.brand === optionValue;
          else if (category === 'gender') selfMatch = product.gender === optionValue;
          else if (category === 'origin') selfMatch = product.origin === optionValue;
          else if (category === 'movement') selfMatch = product.movement === optionValue;
          
          if (!selfMatch) return false;

          // For all OTHER categories, check against the active filters
          if (category !== 'brands' && brands.length > 0 && !brands.includes(product.brand)) return false;
          if (category !== 'gender' && gender.length > 0 && !gender.includes(product.gender)) return false;
          if (category !== 'origin' && origin.length > 0 && !origin.includes(product.origin)) return false;
          if (category !== 'movement' && movement.length > 0 && !movement.includes(product.movement)) return false;

        return true;
        }).length;
        
        return { value: optionValue, count };
      });
    };

    if (initialLoading) {
      return { brands: [], gender: [], origin: [], movement: [] };
    }

    return {
      brands: calculateCounts('brands'),
      gender: calculateCounts('gender'),
      origin: calculateCounts('origin'),
      movement: calculateCounts('movement'),
    };
  }, [activeFilters, allProducts, masterFilterOptions, initialLoading]);
  
  // Handle filter changes from the Filter component
  const handleFilterChange = useCallback((newFilters) => {
    // This function now just updates the URL search params.
    // The useEffect listening to searchParams will update activeFilters,
    // which in turn triggers the useMemo for filtering.
    const newSearchParams = new URLSearchParams();
    
    Object.entries(newFilters).forEach(([key, value]) => {
      if (key === 'priceRange') {
        if (value.min > 0) newSearchParams.set('minPrice', value.min.toString());
        if (value.max < 1000000) newSearchParams.set('maxPrice', value.max.toString());
      } else if (Array.isArray(value) && value.length > 0) {
        newSearchParams.set(key, value.join(","));
      }
    });
    
    setSearchParams(newSearchParams, { replace: true });
  }, [setSearchParams]);

  const handleSortChange = useCallback((newSortOption) => {
    const newSearchParams = new URLSearchParams(searchParams);
    if (newSortOption === 'default') {
      newSearchParams.delete('sort');
    } else {
      newSearchParams.set('sort', newSortOption);
    }
    setSearchParams(newSearchParams, { replace: true });
  }, [searchParams, setSearchParams]);

  const activeFilterCount = useMemo(() => {
    const { brands, gender, origin, movement, priceRange } = activeFilters;
    const arrayFiltersCount = brands.length + gender.length + origin.length + movement.length;
    const isPriceRangeActive = priceRange.min > 0 || priceRange.max < 1000000;
    return arrayFiltersCount + (isPriceRangeActive ? 1 : 0);
  }, [activeFilters]);

  if (initialLoading) {
    return <div className="container mx-auto p-16 mt-24 text-center">Loading products...</div>;
  }

  return (
    <div className="container mx-auto px-4 pt-24">
      <div className="flex flex-col md:flex-row gap-x-6">
        {/* Filter Sidebar */}
        <div className="w-full md:w-96 flex-shrink-0 hidden md:block">
          <Filter 
            filterOptions={filterOptionsWithCounts} 
            onFilterChange={handleFilterChange}
          />
        </div>
      
        {/* Product Grid and Header */}
        <div className="flex-grow">
          {/* Mobile Filter Button */}
          
          <div className="flex justify-between items-center mb-4">
            <div className="text-lg font-semibold text-gray-700">
              {sortedProducts.length > 0 
                ? `${sortedProducts.length} product${sortedProducts.length === 1 ? '' : 's'} found` 
                : 'No products found'}
            </div>
            
            <div className="flex items-center">
              <label htmlFor="sort-options" className="text-sm font-medium text-gray-700 mr-2 whitespace-nowrap">Sort by:</label>
              <select 
                id="sort-options"
                name="sort-options"
                className="block w-full pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm rounded-md shadow-sm"
                value={sortOption}
                onChange={(e) => handleSortChange(e.target.value)}
              >
                <option value="default">Default</option>
                <option value="price-asc">Price: Low to High</option>
                <option value="price-desc">Price: High to Low</option>
                <option value="ref-asc">Ref. Number: A-Z</option>
                <option value="ref-desc">Ref. Number: Z-A</option>
              </select>
            </div>
          </div>
          
          <h1 className="text-2xl hidden font-bold mb-5">Store</h1>
          
          <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
            {displayedProducts.map((product) => {
              const outOfStock = (product.muthanaQty || 0) === 0 && (product.royalQty || 0) === 0 && (product.whQty || 0) === 0;
              const productWithStock = { ...product, outOfStock };
                      
              return (
                <div key={product.id} onClick={handleProductClick}>
                  <ProductCard product={productWithStock} />
                </div>
              );
            })}
          </div>
          
          <div ref={loadingRef} className="h-10" />

          {sortedProducts.length === 0 && !initialLoading && (
            <div className="text-center p-4 mt-4">
              No products found matching your filters
            </div>
          )}
        </div>
      </div>

      {/* Mobile Floating Filter Button */}
      <div className="md:hidden fixed bottom-6 left-6 z-30">
        <button
          onClick={() => setIsFilterModalOpen(true)}
          className="relative bg-black text-white p-4 rounded-full shadow-lg hover:bg-gray-800 transition-transform transform hover:scale-110 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-black"
          aria-label={`Open filters (${activeFilterCount} active)`}
        >
          <img
            src="/Icons and Logos/settings-sliders.svg"
            alt="Open Filters"
            className="invert w-6 h-6"
          />
          {activeFilterCount > 0 && (
            <span className="absolute -top-1 -right-1 flex h-5 w-5 items-center justify-center rounded-full bg-red-500 text-xs font-bold text-white">
              {activeFilterCount}
            </span>
          )}
        </button>
      </div>

      {/* Mobile Filter Modal */}
      {isFilterModalOpen && (
        <div 
          className="fixed inset-0 bg-black bg-opacity-50 z-40 md:hidden" 
          onClick={() => setIsFilterModalOpen(false)}
          aria-hidden="true"
        ></div>
      )}
      <div 
        className={`fixed bottom-0 left-0 right-0 bg-white shadow-2xl rounded-t-lg p-4 z-50 transform transition-transform duration-300 ease-in-out md:hidden ${isFilterModalOpen ? 'translate-y-0' : 'translate-y-full'} max-h-[85vh] flex flex-col`}
        role="dialog"
        aria-modal="true"
      >
        <div className="flex-grow overflow-y-auto">
          <Filter 
            filterOptions={filterOptionsWithCounts} 
            onFilterChange={handleFilterChange}
            onModalClose={() => setIsFilterModalOpen(false)}
          />
        </div>
      </div>
    </div>
  );
}

export default StorePage;